import axios from "axios";
import crypto from "@/plugins/crypto";
//import { stringify } from "querystring";
import to from "await-to-js";
import app from "@/main";
const apiSite = process.env.NODE_ENV
  ? "/v1"
  : `${process.env.VUE_APP_USDT_API}/v1`;
const instance = axios.create({
  baseURL: `/api`,
  timeout: 9000,
  responseType: "json", // Add this line to set the default responseType
});
instance.interceptors.request.use(
  (config) => {
    // const token = app.$store.state.user.token;
    // if (token) config.headers.iToken = token;
    Object.assign(config.headers, {
      "Content-Type": "application/json",
      "T-API-KEY": crypto.ApiKey,
    });
    config.params = Object.assign(config.params || {}, {
      ajax: 1,
    });
    if (!config.noAutoFormData) {
      config.baseURL = `${apiSite}`;
      if (config.method.toUpperCase() === "POST") {
        const s = crypto.aesCBCEncrypt(JSON.stringify(config.data));
        config.data = {
          s,
        };
      }
    }
    return config;
  },
  (err) => Promise.reject(err)
);
//不需要默认提示的code；9991 用于调起Authenticator验证码组件   code:9990 充值未成功 与约定为不予展示
const noTipsCode = [9990];
instance.interceptors.response.use(
  (res) => {
    const result = res.data;
    const { code, data, msg } = result;
    if (code && code !== 200) {
      if (!noTipsCode.includes(code)) {
        app.$message.error(msg);
      }
      return Promise.reject(result);
    }
    if (data) {
      Object.assign(result, {
        data: crypto.aesCBCDecrypt(data),
      });
    }
    return result;
  },
  async (err) => {
    //app.$message.error("网络错误，请稍后重试");
    return Promise.reject(err);
  }
);

// 2. 导出一个通过新axios实例调用接口的函数
export default ({
  url,
  method = "get",
  params,
  data,
  headers,
  responseType,
  noAutoFormData = true,
}) => {
  const promise = instance({
    url,
    method,
    params,
    data,
    responseType,
    headers,
    noAutoFormData,
  });
  return to(promise);
};
