<template>
  <div class="layout-section justify-between">
    <div class="center-center top-slide d-flex" @click="open">
      <i class="el-icon-s-unfold icon"></i>
    </div>
    <div class="center-center">
      <a href="/">
        <img src="@/assets/logo.png" alt="" />
      </a>
    </div>
    <div class="center-center top-slide" @click="dialogFormVisible = true">
      <i class="el-icon-search icon"></i>
    </div>
    <el-dialog
      :modal-append-to-body="true"
      :append-to-body="true"
      :visible.sync="dialogFormVisible"
      :width="isMobile ? '98%' : '40%'"
    >
      <el-form :model="form" @submit.native.prevent="search">
        <el-form-item>
          <el-input v-model.trim="form.text" autocomplete="off"></el-input>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="dialogFormVisible = false">取 消</el-button>
        <el-button type="primary" @click="search">搜索</el-button>
      </div>
    </el-dialog>
  </div>
</template>

<script>
export default {
  name: "AppSction",
  data() {
    return {
      dialogFormVisible: false,
      form: {
        text: "",
      },
    };
  },
  props: {
    title: {
      type: String,
      default: "",
    },
  },
  computed: {
    slideShow() {
      return this.$store.state.slideShow;
    },
    isMobile() {
      return this.$store.state.isMobile;
    },
  },
  methods: {
    open() {
      this.$store.commit("setSlideShow", !this.slideShow);
    },
    search() {
      if (!this.form.text) return;
      if (this.form.text.length > 20) {
        return this.$message.error("搜索内容不能超过20个字符");
      }
      this.dialogFormVisible = false;
      this.$router.push({
        path: "/search/javs",
        query: {
          search_query: this.form.text,
        },
      });
      this.form.text = "";
    },
  },
};
</script>
<style scoped lang="scss">
.transform-90 {
  transform: rotate(90deg);
}
.top-slide {
  padding: 0 24px;
}
.icon {
  color: $--color-primary;
  font-size: 30px;
  font-weight: bold;
}
</style>
