<template>
  <el-dialog
    class="js-thumbnail-vip"
    :visible.sync="dialogVisible"
    :width="isMobile ? '96%' : `30%`"
    title="售後郵箱:armandolailah70591@gmail.com"
  >
    <div v-loading="loading">
      <el-form
        :inline="true"
        :model="formInline"
        ref="dynamicValidateForm"
        class="demo-form-inline"
      >
        <el-form-item
          prop="text"
          class="input-list"
          :rules="{
            required: true,
            trigger: 'blur',
          }"
        >
          <el-input
            v-model="formInline.text"
            placeholder="已有密碼,輸入VIP播放密碼"
          ></el-input>
        </el-form-item>
        <el-form-item>
          <el-button type="primary" @click="onSubmit('dynamicValidateForm')"
            >確定</el-button
          >
        </el-form-item>
      </el-form>
      <PlaneVip @buy="buy" />
    </div>
  </el-dialog>
</template>

<script>
import { mapState } from "vuex";
import vipCrypto from "@/plugins/vipCrypto";
import apiUser from "@/api/user";
import { uid } from "uid";
export default {
  name: "ThumbnailVip",
  data() {
    return {
      loading: false,
      dialogVisible: false,
      formInline: { text: "", type: "text" },
      addressList: [],
    };
  },
  computed: {
    ...mapState(["isMobile"]),
    curAdr() {
      if (!this.addressList.length) return "";
      return this.addressList[0].address;
    },
  },
  methods: {
    async show() {
      this.dialogVisible = true;
      await this.queryAddressList();
    },
    async queryAddressList() {
      this.loading = true;
      const [error, res] = await apiUser.AddressList();
      this.loading = false;
      if (error) return;
      this.addressList = res.data;
    },
    onSubmit(formName) {
      this.$refs[formName].validate((valid) => {
        if (!valid) return;
        const uesr = vipCrypto.getToken(this.formInline.text);
        if (!uesr) {
          this.$message.error("VIP密碼錯誤，请升级VIP");
          return;
        }
      });
    },
    async buy() {
      const data = {
        legal_tender_money: 25,
        currency_type: "USDT",
        out_order_no: uid(),
        address: this.curAdr,
        notify_url: "http://www.xxx.com/notify",
      };
      this.loading = true;
      const [error, res] = await apiUser.CreateOrder(data);
      console.log(error, res);
      this.loading = false;
    },
  },
};
</script>
<style scoped lang="scss">
.player-components {
  position: relative;
}
.desc {
  font-size: 16px;
  font-weight: 700;
}
</style>
