// 提供三个函数，设置token  获取token  清除token
import axios from "axios";
const KEY = "orders";
const aeskey = "dfdfd56dfdlkl";
import CryptoJS from "crypto-js";
export default {
  setToken(token, k = "") {
    console.log("setToken ", token);
    let inKey = k || KEY;
    if (inKey === KEY) {
      token = CryptoJS.AES.encrypt(JSON.stringify(token), aeskey).toString();
    }
    localStorage.setItem(inKey, token);
  },
  decrypt(str) {
    let url = "";
    try {
      url = CryptoJS.AES.decrypt(str, aeskey).toString(CryptoJS.enc.Utf8);
    } catch (e) {
      alert("解密失败");
    }
    return url;
  },
  getToken(k = "") {
    let inKey = k || KEY;
    if (inKey === KEY) {
      let token = localStorage.getItem(inKey) || "";
      if (token) {
        try {
          token = CryptoJS.AES.decrypt(token, aeskey).toString(
            CryptoJS.enc.Utf8
          );
          token = JSON.parse(token);
        } catch (e) {
          token = "";
        }
      }
      return token;
    }
    return localStorage.getItem(k || KEY) || "";
  },
  clearToken(k = "") {
    localStorage.removeItem(k || KEY);
  },
  group(arr, filed) {
    let temObj = {};
    for (let i = 0; i < arr.length; i++) {
      let item = arr[i];
      if (!temObj[item[filed]]) {
        temObj[item[filed]] = [item];
      } else {
        temObj[item[filed]].push(item);
      }
    }
    let resArr = [];
    Object.keys(temObj).forEach((key) => {
      const doc = {
        data: temObj[key],
      };
      doc[`${filed}`] = key;
      resArr.push(doc);
    });
    return resArr;
  },
  getBase64(url) {
    return axios
      .get(`${process.env.VUE_APP_PROXY_SITE}${url}`)
      .then((res) => {
        return res.data;
      })
      .catch(() => "");
  },
};
