<template>
  <el-image
    class="layout-list-item-img"
    style="width: 100%"
    :src="base64"
    lazy
    :error="error"
    fit="cover"
  >
    <div slot="error" class="img-error">
      <i class="el-icon-picture-outline"></i>
    </div>
  </el-image>
</template>

<script>
import auth from "@/plugins/auth";
export default {
  name: "AppLazyImg",
  data() {
    return {
      base64: "",
    };
  },
  props: {
    src: {
      type: String,
      default: "",
    },
  },
  methods: {
    async error() {
      this.base64 = await auth.getBase64(this.src);
    },
  },
  created() {
    this.error();
  },
};
</script>
<style lang="scss">
.img-error {
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 14px;
  background: #f5f7fa;
  color: #c0c4cc;
  vertical-align: middle;
  height: 100%;
  .el-icon-picture-outline {
    font-size: 60px;
  }
}
</style>
