import Vue from "vue";
import VueRouter from "vue-router";
import AppSlide from "@/components/global/AppSlide";
import AppTopBar from "@/components/global/AppTopBar";
import store from "@/store/index";
Vue.use(VueRouter);
const routes = [
  {
    path: "/",
    name: "home",
    components: {
      default: () => import("@/views/HomeView.vue"),
      AppSlide: AppSlide,
      AppTopBar: AppTopBar,
    },
  },
  {
    path: "/javs/:id.html*",
    name: "detail",
    components: {
      default: () => import("@/views/DetailView"),
      AppSlide: AppSlide,
      AppTopBar: AppTopBar,
    },
  },
  {
    path: "/search/javs",
    name: "search",
    components: {
      default: () => import("@/views/HomeView"),
      AppSlide: AppSlide,
      AppTopBar: AppTopBar,
    },
  },
  {
    path: "/cat/:id/:p?",
    name: "category",
    components: {
      default: () => import("@/views/HomeView"),
      AppSlide: AppSlide,
      AppTopBar: AppTopBar,
    },
  },
  {
    path: "/chatplane/:id",
    name: "ChatView",
    components: {
      default: () => import("@/views/ChatView"),
      AppSlide: AppSlide,
      AppTopBar: AppTopBar,
    },
  },
  {
    path: "/result",
    name: "Result",
    components: {
      default: () => import("@/views/ResultView"),
      AppSlide: AppSlide,
      AppTopBar: AppTopBar,
    },
  },
  {
    path: "*",
    name: "NotFound",
    redirect: "/",
  },
];
//  base: process.env.VUE_APP_PATH,

const router = new VueRouter({
  mode: "history",
  routes,
  scrollBehavior(to, from, savedPosition) {
    return savedPosition || { x: 0, y: 0 };
  },
});
router.beforeEach(async (to, from, next) => {
  if (store.state.isMobile) {
    store.commit("setSlideShow", false);
  }
  store.commit("setSomeKey", {
    key: "currentTalkId",
    value: "",
  });
  next();
});
export default router;
