<template>
  <div id="app" :style="Style">
    <router-view
      class="app-top-bar"
      :style="{ height: topBarHeight }"
      name="AppTopBar"
    ></router-view>
    <div class="d-flex app-view-cont">
      <router-view
        :style="{ width: slideWidth }"
        class="app-slide"
        name="AppSlide"
        :key="`${$route.fullPath}-AppSlide`"
        v-show="slideShow"
      ></router-view>
      <router-view :key="$route.fullPath" class="route-view-cont flex-1" />
    </div>
    <el-dialog
      title="极速无广告看片"
      :modal-append-to-body="true"
      :append-to-body="true"
      :visible.sync="dialogVisible"
      :close-on-click-modal="false"
      :width="isMobile ? '98%' : '40%'"
      class="buy-vip-dialog"
    >
      <div class="host-list">
        <h4>今日最新加速网址:</h4>
        <ul>
          <li v-for="(item, idx) in site.today" :key="`${idx}`">
            {{ item }}
          </li>
        </ul>
        <h4 style="margin-top: 20px">主站网址:</h4>
        <ul>
          <li v-for="(item, idx) in site.now" :key="`${idx}`">{{ item }}</li>
        </ul>
        <h4 style="margin-top: 10px">优先使用加速网址观看，加速爽看</h4>
      </div>
      <span slot="footer" class="dialog-footer">
        <el-button
          type="primary"
          v-clipboard:copy="text"
          v-clipboard:success="onCopy"
          v-clipboard:error="onError"
          >已知晓</el-button
        >
      </span>
    </el-dialog>
  </div>
</template>
<script>
import apiUser from "@/api/user";
import auth from "@/plugins/auth";
import $ from "jquery";
window.$ = $;
import { mapGetters } from "vuex";
export default {
  name: "App",
  data() {
    return {
      today: null,
      slideWidth: "280px",
      topBarHeight: "62px",
      oldScrollTop: 0,
      isHide: false,
      dialogVisible: false,
    };
  },
  computed: {
    site() {
      return this.$store.state.site;
    },
    text() {
      return `${this.site.today}，${this.site.now}`;
    },
    isMobile() {
      return this.$store.state.isMobile;
    },
    slideShow() {
      return this.$store.state.slideShow;
    },
    slideStyle() {
      return {
        width: this.slideWidth,
      };
    },
    topBarStyle() {
      return {
        height: this.topBarHeight,
      };
    },
    Style() {
      return {
        paddingTop: this.topBarHeight,
      };
    },
    userId() {
      return this.$store.state.userId;
    },
    ...mapGetters(["skipHref"]),
  },
  methods: {
    now() {
      const currentDate = new Date();
      const year = currentDate.getFullYear();
      const month = String(currentDate.getMonth() + 1).padStart(2, "0");
      const day = String(currentDate.getDate()).padStart(2, "0");
      this.today = `${year}-${month}-${day}`;
    },
    onCopy(e) {
      this.$message({
        message: `复制成功保存好网址：${e.text}`,
        type: "success",
      });
      this.dialogVisible = false;
      if (this.skipHref) {
        window.open(this.skipHref);
      }
    },
    onError() {
      this.$message({
        message: `复制失败`,
        type: "error",
      });
    },
    handleScroll() {
      const scrollTop =
        window.pageYOffset ||
        document.documentElement.scrollTop ||
        document.body.scrollTop;
      if (scrollTop > this.oldScrollTop) {
        this.isHide = true;
      } else {
        this.isHide = false;
      }
      this.oldScrollTop = scrollTop;
    },
  },
  async created() {
    this.now();
    const [err, res] = await apiUser.sites();
    if (err) return;
    this.$store.commit("setSite", res);
    const day = auth.getToken("today");
    if (day !== this.today) {
      this.dialogVisible = true;
      auth.setToken(this.today, "today");
    }
    for (let keys in this.site) {
      if (keys.includes("html")) {
        $("body").append(this.site[keys]);
      }
    }
  },
  mounted() {
    // window.addEventListener("scroll", this.handleScroll);
  },
};
</script>
