<template>
  <div>
    <div id="dplayer" :vip="isVip" ref="player" class="play-root" />
    <p v-if="orders.out_trade_no" style="padding: 4px 0; color: red">
      复制保存好订单号，丢失将无法找回！！
    </p>
    <el-form
      status-icon
      ref="ruleForm"
      v-if="orders.out_trade_no"
      class="demo-ruleForm"
    >
      <el-form-item class="check-order-detail">
        <el-input v-model.trim="orders.out_trade_no"></el-input>
        <el-button
          type="primary"
          v-clipboard:copy="orders.out_trade_no"
          v-clipboard:success="onCopy"
          v-clipboard:error="onError"
          >复制</el-button
        >
      </el-form-item>
    </el-form>
    <VideoVip :vip="isVip" :video="video" ref="VideoVip" />
  </div>
</template>

<script>
import apiUser from "@/api/user";
import VideoVip from "@/components/global/VideoVip";
import auth from "@/plugins/auth";
export default {
  name: "PlayerVideo",
  components: {
    VideoVip,
  },
  data() {
    return {
      dp: null,
      user: "",
      img: "",
    };
  },
  props: {
    needVip: {
      type: Boolean,
      default: true,
    },
    video: {
      type: Object,
      default: () => {
        return {
          source: "",
          url: "",
          _id: "",
        };
      },
    },
  },
  computed: {
    orders() {
      return this.$store.state.orders;
    },
    isVip() {
      return this.orders._id && Date.now() < this.orders.date;
    },
  },
  methods: {
    onCopy(e) {
      this.$message({
        message: `复制成功：${e.text}`,
        type: "success",
      });
      this.copyVisible = false;
      const buyId = auth.getToken("buyId");
      if (buyId) {
        this.$router.push(`/javs/${buyId}.html`);
      }
    },
    onError() {
      this.$message({
        message: `复制失败`,
        type: "error",
      });
    },
    loadVideo() {
      //console.log(this.isVip, this.orders);
      // eslint-disable-next-line no-undef
      window.dp = this.dp = new DPlayer({
        container: this.$refs.player,
        autoplay: false,
        video: {
          ...this.video,
          url: auth.decrypt(`${this.video.url}`),
        },
      });
      this.dp.on("timeupdate", () => {
        //currentTime > 100 && (currentTime - 100) % 10 === 0
        const time = parseInt(this.dp.video.currentTime);
        if (time > 20 && (time - 20) % 3 === 0 && !this.isVip) {
          this.dp.pause();
          this.dp.fullScreen.cancel("web");
          if (!this.$refs.VideoVip.dialogVisible) {
            this.$refs.VideoVip.show();
          }
        }
      });
    },
    async queryApplyInfo() {
      if (!this.orders._id) return;
      const [err, res] = await apiUser.vaidOrder({
        out_trade_no: this.orders.out_trade_no,
        code: 9990,
      });
      if (err) {
        this.$store.commit("clearOrders");
        return;
      }
      const { result } = res;
      this.$store.commit("setOrders", result);
    },
  },
  mounted() {
    this.$nextTick(() => {
      this.loadVideo();
    });
  },
  beforeDestroy() {
    this.dp && this.dp.destroy();
  },
  async created() {
    this.queryApplyInfo();
  },
};
</script>
<style lang="scss">
.check-order-detail {
  .el-form-item__content {
    display: flex;
    align-items: center;
  }
}
</style>
