import Vue from "vue";
import App from "./App.vue";
//import "./registerServiceWorker";
import router from "./router";
import store from "./store";
import globalComponents from "@/components/global";
import VueClipboard from "vue-clipboard2";
Vue.use(VueClipboard);
Vue.use(globalComponents);
import {
  Button,
  Row,
  Col,
  Card,
  Image,
  Icon,
  Tabs,
  TabPane,
  Badge,
  Avatar,
  Form,
  Input,
  FormItem,
  Message,
  dialog,
  upload,
  Loading,
  Tag,
  Pagination,
  result,
} from "element-ui";
import "@/assets/style/element-variables.scss";
import "@/assets/style/comm.scss";
Vue.use(Tag);
Vue.use(Button);
Vue.use(Row);
Vue.use(Col);
Vue.use(Card);
Vue.use(Image);
Vue.use(Icon);
Vue.use(Tabs);
Vue.use(TabPane);
Vue.use(Badge);
Vue.use(Avatar);
Vue.use(Form);
Vue.use(Input);
Vue.use(FormItem);
Vue.use(dialog);
Vue.use(upload);
Vue.use(Loading);
Vue.use(Pagination);
Vue.use(result);
Message.install = (Vue) => {
  Vue.prototype.$message = Message;
};
Vue.use(Message); //消息提示
Vue.config.productionTip = false;
const appVue = new Vue({
  router,
  store,
  render: (h) => h(App),
}).$mount("#app");
export default appVue;
