      rules: {
        avatar: [{ required: true, trigger: "blur" }],
        age: [{ required: true, trigger: "blur" }],
        name: [{ required: true, trigger: "blur" }],
      },
    };
  },
  computed: {
    site() {
      return this.$store.state.site;
    },
    nav() {
      return this.$store.state.nav;
    },
    ...mapState(["userId", "userAarry", "user", "isMobile", "msgdata"]),
    ...mapGetters(["myself", "myChatList"]),
    unread() {
      return this.msgdata.filter((v) => !v.isRead).length;
    },
  },
  name: "AppTopBar",
  methods: {
    initIo() {
      this.socket.on("tail-all", (msg) => {
        this.$store.dispatch("setUserAarry", msg);
      });
    },
    goTalk(id) {
      // if (Object.keys(this.user).length === 0) {
      //   this.dialogVisible = true;
      //   return;
      // }
      this.$router.push(`/chatplane/${id}`);
    },
    handleAvatarSuccess(res, file) {
      this.imageUrl = URL.createObjectURL(file.raw);
    },
    beforeAvatarUpload(file) {
      const isJPG = file.type.includes("image/");
      const isLt5M = file.size / 1024 / 1024 < 5;

      if (!isJPG) {
        this.$message.error(
          "Avatar picture can only be JPG,PNG,JPEG,WEBP format!"
        );
      }
      if (!isLt5M) {
        this.$message.error("Avatar picture size cannot exceed 5MB!");
      }
      return isJPG && isLt5M;
    },
    submitForm(formName) {
      this.$refs[formName].validate((valid) => {
        if (valid) {
          alert("submit!");
        } else {
          console.log("error submit!!");
          return false;
        }
      });
    },
    resetForm(formName) {
      this.$refs[formName].resetFields();
    },
    countUnread(id) {
      return this.msgdata.filter((v) => v.to === id && !v.isRead).length;
    },
  },
};
