<template>
  <div>
    <el-dialog
      :modal-append-to-body="true"
      :append-to-body="true"
      :visible.sync="dialogVisible"
      :close-on-click-modal="false"
      :width="isMobile ? '98%' : '40%'"
      class="buy-vip-dialog"
      :before-close="closed"
      title="成为VIP无广告，安静的看视频"
    >
      <div v-loading="loading">
        <el-form
          :model="ruleForm"
          status-icon
          :rules="rules"
          ref="ruleForm"
          class="demo-ruleForm"
        >
          <el-form-item prop="out_trade_no" class="check-vip">
            <el-input
              v-model.trim="ruleForm.out_trade_no"
              placeholder="已购买？输入订单号查询"
            ></el-input>
            <el-button type="primary" @click="submitForm('ruleForm')"
              >查询</el-button
            >
          </el-form-item>
          <ul
            class="vip-list"
            v-for="(item, idx) in vipList"
            :key="`${idx}`"
            @click="curIndex = idx"
          >
            <li>
              <h2>{{ item.name }}</h2>
            </li>
            <li>￥{{ item.price }}</li>
            <li>{{ item.desc }}</li>
            <li>
              <el-button
                type="primary"
                class="vip-buy"
                size="small"
                @click="CreateOrder('alipay')"
              >
                <img width="26" src="@/assets/alilogo_small.png" />
                支付宝开通</el-button
              >
            </li>
            <li>
              <el-button
                type="primary"
                class="vip-buy"
                size="small"
                @click="CreateOrder('wxpay')"
              >
                <img width="26" src="@/assets/wxlogo_small.png" />
                微信开通</el-button
              >
            </li>
          </ul>
        </el-form>
      </div>
    </el-dialog>
    <el-dialog
      title="请勿关闭页面，支付成功后点击确认"
      :modal-append-to-body="true"
      :append-to-body="true"
      :visible.sync="checkVisible"
      :close-on-click-modal="false"
      :width="isMobile ? '98%' : '40%'"
      class="buy-vip-dialog"
    >
      <div v-loading="loading">
        <span>确认是否支付成功？</span>
        <span slot="footer" class="dialog-footer">
          <el-button type="primary" @click="sure">确认支付成功</el-button>
        </span>
      </div>
    </el-dialog>
    <el-dialog
      title="复制保存好订单号，丢失将无法找回！！"
      :modal-append-to-body="true"
      :append-to-body="true"
      :visible.sync="copyVisible"
      :close-on-click-modal="false"
      :width="isMobile ? '98%' : '40%'"
      class="buy-vip-dialog"
    >
      <div v-loading="loading">
        <el-form status-icon ref="ruleForm" class="demo-ruleForm">
          <el-form-item class="check-vip">
            <el-input v-model.trim="orders.out_trade_no"></el-input>
            <el-button
              type="primary"
              v-clipboard:copy="orders.out_trade_no"
              v-clipboard:success="onCopy"
              v-clipboard:error="onError"
              >复制</el-button
            >
          </el-form-item>
        </el-form>
      </div>
    </el-dialog>
  </div>
</template>

<script>
import { MessageBox } from "element-ui";
import { mapGetters } from "vuex";
import auth from "@/plugins/auth";
import apiUser from "@/api/user";
export default {
  name: "videoVip",
  props: {
    video: {
      type: Object,
      default: () => {
        return {
          source: "",
          url: "",
          _id: "",
        };
      },
    },
  },
  data() {
    return {
      num: 0,
      checkVisible: false,
      loading: false,
      dialogVisible: false,
      copyVisible: false,
      ruleForm: {
        out_trade_no: "",
      },
      rules: {
        out_trade_no: [
          { required: true, message: "请输入订单号", trigger: "blur" },
          // {
          //   min: 8,
          //   max: 80,
          //   message: "订单号格式错误",
          //   trigger: "blur",
          // },
        ],
      },
      curIndex: 0,
      order: {},
      vipList: [
        {
          name: "月卡VIP",
          price: 98,
          desc: "特惠！31天,随心所欲观看视频不受限制",
        },
      ],
      playList: [
        {
          name: "支付宝",
          type: "alipay",
        },
        {
          name: "微信",
          type: "wxpay",
        },
      ],
    };
  },
  computed: {
    ...mapGetters(["skipHref"]),
    orders() {
      return this.$store.state.orders;
    },
    curItem() {
      return this.vipList[this.curIndex];
    },
    isMobile() {
      return this.$store.state.isMobile;
    },
  },
  methods: {
    show() {
      this.dialogVisible = true;
      this.num++;
      console.log(this.num);
    },
    closed() {
      this.dialogVisible = false;
      if (this.num % 2 !== 0 && this.skipHref) {
        window.open(this.skipHref);
      }
    },
    onCopy(e) {
      this.$message({
        message: `复制成功：${e.text}`,
        type: "success",
      });
      this.copyVisible = false;
    },
    onError() {
      this.$message({
        message: `复制失败`,
        type: "error",
      });
    },
    sleep(t) {
      return new Promise((resolve) => {
        setTimeout(() => {
          resolve();
        }, 1000 * t);
      });
    },
    async sure() {
      this.loading = true;
      await this.sleep(6);
      this.vaidOrder({
        out_trade_no: this.order.out_trade_no,
      });
    },
    async vaidOrder(params) {
      this.loading = true;
      const [err, res] = await apiUser.vaidOrder(params);
      this.loading = false;
      if (err) return;
      this.checkVisible = false;
      this.dialogVisible = false;
      const { result } = res;
      this.$store.commit("setOrders", result);
      this.dialogVisible = false;
      this.copyVisible = true;
      this.$message({
        message: "您已开通VIP，可观看所有视频",
        type: "success",
      });
    },
    submitForm(formName) {
      this.$refs[formName].validate(async (valid) => {
        if (!valid) return;
        this.vaidOrder({
          out_trade_no: this.ruleForm.out_trade_no,
        });
      });
    },
    async CreateOrder(p) {
      this.order = {};
      this.loading = true;
      const item = this.playList.find((v) => v.type === p);
      const [err, res] = await apiUser.CreateOrder({
        type: item.type,
        price: this.curItem.price,
      });
      this.loading = false;
      if (err) return;
      this.order = res;
      auth.setToken(this.video._id, "buyId");
      let sortedKeys = Object.keys(res).sort();
      let path = sortedKeys
        .map(function (key) {
          let value = res[key];
          let keyValueString = key + "=" + value;
          return keyValueString;
        })
        .join("&");
      MessageBox.confirm(
        `您选择了${item.name} ￥${this.curItem.price}`,
        "提示",
        {
          confirmButtonText: "确定",
          cancelButtonText: "取消",
        }
      )
        .then(() => {
          this.checkVisible = true;
          if (item.type === "alipay") {
            window.open(`https://pay.kuangtianxia.cn/submit.php?${path}`);
          }
          if (item.type === "wxpay") {
            window.open(`https://www.188.im/submit.php?${path}`);
          }
        })
        .catch(() => {});
    },
  },
};
</script>
