// 提供三个函数，设置token  获取token  清除token
import crypto from "@/plugins/crypto";
const KEY = process.env.VUE_APP_TOKEN_NAME;
export default {
  setToken(token) {
    localStorage.setItem(KEY, crypto.aesCBCEncrypt(token));
  },
  getToken() {
    console.log("getToken", KEY);
    const token = localStorage.getItem(KEY) || "";
    if (token) {
      return crypto.aesCBCDecrypt(token);
    }
    return "";
  },
  clearToken() {
    localStorage.removeItem(KEY);
  },
};
