import Vue from "vue";
import Vuex from "vuex";
import nav from "@/nav";
import auth from "@/plugins/auth";
const isMobile = () => {
  let flag =
    /Android|webOS|iPhone|iPad|iPod|BlackBerry|IEMobile|Opera Mini/i.test(
      navigator.userAgent
    );
  return flag;
};
Vue.use(Vuex);

export default new Vuex.Store({
  state: {
    site: {
      today: [],
      now: [],
      ip: "",
    },
    orders: auth.getToken(),
    pay: {
      pid: "1030",
    },
    isMobile: isMobile(),
    nav,
    slideShow: isMobile() ? false : true,
    userAarry: [],
    msgdata: [],
    user: {},
    socket: {},
    currentTalkId: "",
    currentChatTime: "",
  },
  getters: {
    myself(state) {
      return state.userAarry.find((item) => item.userId === state.userId);
    },
    currentMsgList(state) {
      return state.msgdata.filter((item) => item.to === state.currentTalkId);
    },
    myChatList(state) {
      return auth.group(state.msgdata, "to");
    },
    skipHref(state) {
      return state.site.href || "";
    },
  },
  mutations: {
    setSomeKey(state, data) {
      state[data.key] = data.value;
    },
    setSlideShow(state, payload) {
      state.slideShow = payload;
    },
    saveUser(state, payload) {
      state.userId = payload;
    },
    saveUserAarry(state, payload) {
      state.userAarry = payload;
    },
    saveMsg(state, payload) {
      //currentTalkId
      state.msgdata.push({
        ...payload,
        isRead: state.currentTalkId === payload.to ? true : false,
      });
    },
    saveCurrentChat(state, payload) {
      state.currentChat = payload;
    },
    saveReadMsg(state, payload) {
      state.msgdata = state.msgdata.map((item) => {
        if (item.to === payload) {
          return {
            ...item,
            isRead: true,
          };
        }
        return item;
      });
    },
    setTradeNo(state, payload) {
      state.out_trade_no = payload;
      auth.setToken(payload);
    },
    clearTradeNo(state) {
      state.out_trade_no = "";
      auth.clearToken();
    },
    clearOrders(state) {
      state.orders = "";
      auth.clearToken();
    },
    setOrders(state, payload) {
      auth.setToken(payload);
      state.orders = payload;
    },
    setSite(state, payload) {
      console.log(payload);
      state.site = payload;
    },
  },
  actions: {
    setUser({ commit }, userId) {
      auth.setToken(userId);
      commit("saveUser", userId);
    },
    setUserAarry({ commit }, data) {
      commit("saveUserAarry", data);
    },
    setMsg({ commit }, data) {
      commit("saveMsg", data);
    },
  },

  modules: {},
});
